import { Component, Event, EventEmitter, h, Host, Method, Prop } from '@stencil/core';
import videojs from 'video.js';
import { IVideoData } from './storm-video-data';
@Component({
  tag: 'storm-video',
  styleUrl: 'storm-video.css',
  shadow: false,
})
export class StormVideo {
  @Prop() dataSet: IVideoData;
  @Prop() autoplay: boolean = false;
  @Prop() loop: boolean = false;
  @Prop() url: string;
  @Prop() reTime: boolean = false;
  videoRef: HTMLVideoElement;
  video;

  @Event() change: EventEmitter;

  @Method()
  removeVideo() {
    this.video.dispose();
  }

  @Method()
  playVideo() {
    this.video.play();
  }

  componentDidRender() {
    if (!this.url && !this.dataSet?.url) return;
    this.video = videojs(this.videoRef, {
      controls: true,
      sources: [{ src: this.url ? this.url : this.dataSet?.url }],
      poster: this.dataSet?.poster,
      autoplay: this.autoplay,
      loop: this.loop,
    });
    this.video.on('ended', () => {
      this.change.emit('ended');
      // if(this.reTime) this.video.currentTime(1);
    });
  }

  render() {
    return (
      <Host>
        <video class="video-js vjs-big-play-centered" ref={elm => (this.videoRef = elm)}></video>
      </Host>
    );
  }
}
